.sec {
    &-title {
        font-size: 46px;
        @media screen and (max-width: $screen-xs-max) {
            font-size: 36px;
        }
        font-family: $font-family-serif;
        margin: 15px 0;
    }
    &-subtitle {
        font-size: 18px;
        font-weight: normal;
        margin-bottom: 30px;
    }
    &-copy {
        color: $copy-color;
    }
}

.subtitle {
    font-size: 32px;
    font-family: $font-family-serif;
    margin: 15px 0;
    @media screen and (max-width: $screen-xs-max) {
        text-align: center;
    }
}