.form {
    margin-top: 30px;
    &-line {
        margin-bottom: 10px;
    }
    &-input,
    &-txta {
        width: 80%;
        margin: 10px 0;
        background-color: transparent;
        color: #fff;
        border-radius: 4px;
        border: 2px solid $gray-light;
        padding: 10px 20px;
        transition: border .4s ease-out 0s, padding .4s ease-out .4s, width .4s ease-out .4s, margin .4s ease-out .4s;
        &:focus {
            border-color: #fff;
            outline: none;
            width: 85%;
            padding: 20px;
            margin: 0px 0;
        }
        &:placeholder {
            color: darken($gray-lighter, 15%);
        }
    }
    &-empty-err,
    &-validate-err,
    &-res-err    {
        color: $brand-danger;
        margin-bottom: -10px;
        transition: all ease .3s;
        opacity: 0;
        visibility: hidden;
        height: 10px;
    }
    &-res-err{
        display: none;
    }
    &-required {
        font-size: 12px;
        color: $gray-light;
    }
    &-res-success {
        color: $brand-primary;
        font-size: 36px;
    }

    &-results{
        position: fixed;
        z-index: 999999;
        background-color: $gray-darker;
        border-radius: 10px;
        color:#fff;
        padding: 90px 30px;
        left: 50%;
        top: 50%;
        width:85vw;
        transform: translate(-50%, -50%);
        text-align: center;
        display: none;
    }
}