.specs{
    text-align: center;
}
.spec{
    @media screen and (max-width:$screen-xs-max) {
        border-bottom: 1px solid $brand-primary;
        padding-bottom: 15px;
        &:last-child {
            border: none;
            padding-bottom: 0;
        }
    }
    &-title{
        font-size: 28px;
    }
    &-icon {
        margin: 30px 0;
    }
}