.about-hover-img {
    &.is-hover {
        opacity: 1;
    }
}

.form {
    &-empty-err,
    &-validate-err {
        &.is-showed {
            opacity: 1;
            visibility: visible;
            height: auto;
        }
    }
}
.btn-down {
    &.is-scroll {
        left: 95%;
        bottom: 3%;
        right: 5%;
        transform: rotate(180deg);
        background-color: $brand-primary;
        width: 83px;
        height: 83px;
        @media screen and (max-width: $screen-xs-max){
            display: none;
        }
        @media screen and (max-width: $screen-sm-max){
            width: 53px;
            height: 53px;
            padding: 10px;
        }
    }
}