.about {
    background-color: $brand-primary;
    color: #fff;
    position: relative;
    padding-bottom: 0;
    .sec-title,.sec-subtitle{
        position: relative;
        z-index: 1;
    }
    a.media-text {
        color: $brand_secondary;
    }
    &-content {
        font-size: 16px;
    }
    &-copy {
        position: relative;
        z-index: 0;
    }
    &-hover-img {
        position: absolute;
        width: 200px;
        height: 150px;
        z-index: -2;
        opacity: 0;
        pointer-events: none;
        transition: opacity ease-out .3s;
        .spec & {
            z-index: 0;
        }
        img {
            width: 100%;
        }
    }
}