#canvas {
    width: 100%;
    height: 100vh;
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    background-color: $brand-primary;
}

.canvas-wrap {
    position: relative;
    background-color: $brand-primary;
}

div.canvas-content {
    position: relative;
    z-index: 2000;
    color: #fff;
    text-align: center;
    padding-top: 30px;
}

.logo {
    position: absolute;
    z-index: 2;
    top: 2.5%;
    padding: 20px;
    left: 50%;
    margin-left: -45px;
}

.hero-content {
    display: table-cell;
    text-align: center;
    vertical-align: middle;
    width: 100vw;
    height: 100vh;
    padding-top: 0;
    background-color: $brand-primary;

}

.typed-cursor {
    animation: fadeOut 0.8s infinite linear;
    font-weight: bolder;
    font-size: 60%;
}

.hero-title {
    height: 60px;
    font-size: 56px;
    @media screen and (max-width: $screen-xs-max) {
        font-size: 30px;
    }
    position: relative;
    font-family: $font-family-serif;
    z-index: 2;
}

.hero-typed-wr {
    font-size: 34px;
    @media screen and (max-width: $screen-xs-max) {
        font-size: 21px;
    }
}