.works {
    background-color: $gray-lighter;
}

.work {
    border-bottom: 1px solid $brand-primary;
    padding-bottom: 30px;
    margin-bottom: 40px;
    &:last-child {
        border-bottom: none;
        margin-bottom: 0;
    }
    @media screen and (max-width: $screen-xs-max) {
        text-align: center;
        padding-bottom: 40px;
        &:nth-child(even) {
            display: flex;
            flex-flow: column;
            & .sm-6:first-child {
                display: inline-block;
                order: 2;
            }
            & .sm-6:last-child {
                display: inline-block;
                order: 1;
            }
        }
        &:last-child {
            padding-bottom: 0;
            margin-bottom: 0;
        }
    }
    &-tag {
        color: lighten($gray-light, 30%);
        @media screen and (max-width: $screen-xs-max) {
            text-align: center;
        }
        &:before {
            content: "\2022";
            margin-right: 4px;
            color: $brand-primary;
            vertical-align: middle;
            font-size: 2.8rem;
            line-height: 1;
        }
    }
}