[class^=btn] {
    position: relative;
    z-index: 2;
    height: auto;
    padding: 1.2rem 3.2rem;
    font-size: 1.3rem;
    line-height: normal;
    text-transform: uppercase;
    font-family: $font-family-serif;
    font-weight: 700;
    transition: all .3s ease-in-out;
    display: inline-block;
    letter-spacing: 0;
    border-radius: 50px;
    color: #999;
    &:hover {
        text-decoration: none;
    }
}

.btn {
    &-go {
        color: #ffffff;
        margin: 10.0rem 0 0;
        @media screen and (max-width: $screen-xs-max) {
            margin: 3.0rem 0 0;
            width: 90%;
            font-size: 12px;
        }
        padding: 1.4rem 3.2rem;
        font-size: 1.4rem;
        border: 2px solid #FED766;
        &.btn-submit{
            margin: 1rem 0;
            background-color: transparent;
            color:#fff;
            border-color: $brand-primary;
            &:hover {
                background-color: $brand-primary;

            }
        }
        &:hover {
            padding: 1.4rem 4.0rem;
            background-color: $brand_secondary;
            color: $gray-darker;
        }
    }
    &-down {
        bottom: 2.5%;
        position: fixed;
        left: 50%;
        //right: 50%;
        margin-left: -40px;
        border-radius: 50%;
        border: 2px dashed $brand_secondary;
        padding-top: 35px;
        background-color: rgba($brand_primary, .3);
        transition: background-color .5s ease 0s, padding .5s ease 0s, left .5s ease .5s, right .5s ease .5s, transform .5s ease 1s;
    }
    &-default {
        color: $gray-darker;
        margin: 3.0rem 0 0;
        padding: 1.4rem 3.2rem;
        border: 2px solid $brand-primary;
        font-size: 1.4rem;
        &:hover {
            padding: 1.4rem 4.0rem;
            background-color: $gray-darker;
            color: $brand-primary;
            border-color: $gray-darker;
        }
    }
}