html{
    overflow-x: hidden;
}
body {
    margin: 0;
    padding: 0;
}

section {
    padding: 60px 0;
}

a {
    transition: all ease .2s;
    text-decoration: none;
    &:hover {
        text-decoration: none;
    }
    &.sec-link {
        border-bottom-width: 0px;
        &:hover {
            border-bottom: 10px solid $brand_secondary;
        }
    }
}

img {
    &.sec-img {
        width: 100%;
        padding: 0 15px;
    }
}

::selection {
    background-color: $brand-primary;
}

p {
    .about-copy & {
        margin: 1em;
    }
}