.footer {
    background-color: $gray-darker;
    height: 100%;
    min-height: 100%;
    .logo {
        position: relative;
        margin-top: 90px;
        padding: 0;
        margin-left: -25px;
        margin-bottom: 30px;
        display: inline-block;
    }
}

.powered {
    color: $gray-light;
    text-align: center;
    margin-bottom: 30px;
}

.icons {
    text-align: center;
    width: 100%;
    margin-bottom: 40px;
    a {
        position: relative;
        bottom: 0;
        margin: 10px 5px;
        border-radius: 100%; //background-color: $brand-primary;
        width: 32px;
        height: 32px;
        display: inline-block;
        border: 3px solid $brand-primary;
        &:hover {
            background-color: #fff;
            span {
                background-color: #fff;
            }
        }
        &:after {
            content: '';
            position: absolute;
            z-index: 0;
            top: 1px;
            left: 1px;
            display: block;
            border: 0px solid $brand-primary;
            width: 24px;
            height: 24px;
            border-radius: 100%;
            transition: transform .4s ease-out, border .4s ease-out;
        }
        &:hover:after {
            transform: scale(2);
            padding: 6px;
            border: 3px solid $brand-primary;
            background-color: #fff;
        }
        span {
            position: relative;
            border-radius: 100%;
            overflow: hidden;
            margin-top: 1px;
            z-index: 1;
        }
    }
}